body,
html {
    margin: 0;
    padding: 0;
    font-family: "Inter";
}

body {
    text-align: center;
    background-color: #23252c;
}

.profile-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.profile-container > img {
    width: 320px;
    object-fit: fill;
    margin-top: 2.81rem;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}

.info-container {
    background-color: #1a1b21;
    color: #ffffff;
    margin: 0 auto;
    width: 320px;
}

.info-container > h2 {
    font-size: 1.9rem;
    font-weight: 700;
    margin: 0;
    margin-top: 2.8rem;
}

.info-container > h3 {
    font-size: 1.1rem;
    color: #f3bf99;
    font-weight: 400;
    margin-top: 0.375em;
    margin-bottom: 0;
}

.info-container > h4 {
    font-size: 0.9rem;
    font-weight: 400;
    color: #f5f5f5;
    margin-top: 0.55em;
    margin-bottom: 0;
}

.contact-container {
    display: flex;
    background-color: #1a1b21;
    gap: 18px;
    width: 320px;
    justify-content: center;
}

.email,
.linkedin {
    background: white;
    color: #374151;
    padding: 0.5rem 1.78rem;
    border-radius: 4px;
    text-decoration: none;
    margin-top: 1rem;
    font-size: 0.875rem;
    font-weight: 500;
}

.linkedin {
    background: #5093e2;
    color: white;
    padding: 0.5rem 1.1rem;
}

.email:visited,
.linkedin:visited {
    text-decoration: none;
}

.section-container {
    display: flex;
    flex-direction: column;
    width: 320px;
    margin: 0 auto;
    background-color: #1a1b21;
    text-align: left;
}

.section-title {
    margin: 2rem 0 0 0;
    color: white;
    font-size: 1.2rem;
    font-weight: 700;
}

.section-content {
    margin: 0.31rem 0 0 0;
    font-size: 0.8rem;
    color: #dcdcdc;
}

.section-title,
.section-content {
    padding: 0 2.25rem;
}

.space {
    padding-bottom: 1.5rem;
}

.footer-container {
    display: flex;
    width: 320px;
    margin: 0 auto;
    background-color: #161619;
    gap: 1.5rem;
    justify-content: center;
    margin-bottom: 2.75rem;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.footer-icon {
    margin-top: 1.19rem;
    font-size: 1.6rem;
    color: #918e9b;
    padding-bottom: 1.19rem;
}
